
.brandname {
    color: #fff;
    font-size: 30px;
    text-align: center;
    padding: 15px;
}
.switchbox {
    display: none;
}

.switchbox + label {
    width: 70px;
    background: red;
    height: 28px;
    border-radius: 25px;
    position: relative;
    cursor: pointer;
}

.switchbox + label:before {
    position: absolute;
    width: 22px;
    height: 22px;
    background: #ccc;
    content: "";
    border-radius: 50%;
    top: 3px;
    left: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.switchbox:checked + label{
    background-color: green;
    
}
.switchbox:checked + label:before{
    left: 44px;
    
}
.banner-item {
    height: 300px;
    width: calc(33% - 30px);
    margin: 0 15px 15px;
    max-width: calc(330% - 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.banner-row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
}

.banner-item:before {
    content: "";
    background: #000;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    opacity: 0.5;
}

.banner-item button {
    background: #fff;
    margin: 0 10px;
}

div#preloader.transprent {
    background: rgba(0,0,0,0.5);
} 

img.selected_image {
    display: block;
    max-height: 50px;
    object-fit: cover;
    object-position: center center;
}
.btn-primary {
    color: #fff!important;
    background-color: #0294B3!important;
    border-color: #0294B3!important;
}

form.map-form {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
}

form.map-form .container {
    padding: 0;
}

form.map-form div#map {
    height: 400px;
}

form.map-form .form-control {
    margin-bottom: 20px;
}

form.map-form .map-header {
    display: flex;
    margin-bottom: 20px;
    margin-top: 20px;
}

form.map-form .map-header input.pac-target-input {
    margin-right: 13px;
}
.btn-group {
    padding: 20px;
    text-align: center;
    width: 100%;
    display: flex;
    justify-content: center;
}

.btn-group .btn-secondry {
    width: auto;
    max-width: 100px;
    display: inline-block;
}
body[data-sidebar="dark"] #sidebar-menu ul li a.active {
    color:#fefefe;
    font-weight:bold;
}
.approve {
    background: #50b550!important;
    color: #fff!important;
    border: #50b550!important;
}

.disapprove {
    background: red!important;
    color: #fff!important;
