}
.submenu i.ri-arrow-down-s-fill {
    position: absolute;
    right: 4px;
}
.metismenu .mm-collapse {
    padding-left: 5px;
}
.resturnat-details {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.resturnat-detail-row {
    display: flex;
    border-bottom: 1px solid #ccc;
    justify-content: center;
}

.resturnat-detail-row > * {
    width: 50%;
    padding: 14px;
    line-height: 1;
    font-size: 14px;
    margin: 0;
}


.resturnat-detail-row:nth-child(odd) {
    background: #fff;
}

.resturnat-detail-row label {
    font-weight: bold;
    color: #000;
}

.rs-images img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
    margin-right: 20px;
    border: 2px solid #ccc;
}

.rs-images {
    padding: 20px;
}
span.cat {
    padding: 5px 10px;
    display: inline-block;
    background: #981c6d;
    margin-right: 10px;
    border-radius: 5px;
    color: #fff;
}
.resturnat-detail-row > h3 {
    width: 100%;
    font-weight: bold;
}
.main-content {
    overflow-y: auto;
}
.resturnat-detail-row > h3 center {
    color: red;
}


